html,body,
div,span,p,a,
applet,
object,
iframe,
h1,h2,h3,h4,h5,h6,
blockquote,
pre,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,u,i,center,
dl,dt,dd,ol,ul,li,
fieldset,form,label,legend,
caption,table,tr,th,td,thead,tbody,tfoot,
article,aside,canvas,details,embed,
figure,figcaption,footer,header,hgroup,
menu,nav,
output,
ruby,
section,article,
summary,
time,
mark,
audio,video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  outline: none;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
header,footer,section,article {}
aside,
details,
figcaption,
figure,
hgroup,
menu,nav {
  display: block; }

body {
  line-height: 1;
  font-family: 'Montserrat', sans-serif;
  font-size: 23px;
  font-weight: 500; }

blockquote,
q {
  quotes: none; }

ol,ul {
  list-style: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a,
a:visited {
  // color: #000
  text-decoration: none;
  -webkit-transition: all .2s ease-in-out;
  transition: all .3s; }

a:hover,
a:visited:hover {
  text-decoration: none; }

a:focus {
  outline: 0; }

* {
  &::before,
  &::after {
    transition: all .3s; } }

input {
  outline: none; }

 :root {
  --main: #707070;
  --hover: #9FC058; }

@import "_header.sass";
@import "_footer.sass";
@import "index.sass";
@import "adaptive/1200";

.container {
  padding: 0px 15px;
  margin: auto;
  @media screen and ( min-width: 1700px ) {
    width: 1680px; }
  @media screen and ( max-width: 1699px ) {
    width: 1400px; }
  @media screen and ( max-width: 1499px ) {
    width: 1100px; }
  @media screen and ( max-width: 1199px ) {
    width: 700px; }
  @media screen and ( max-width: 767px ) {
    width: 500px; }
  @media screen and ( max-width: 575px ) {
    width: 280; } }
