.header {
  &__logo {
    @media screen and ( max-width: 1199px ) {
      width: 200px;
      img {
        width: 100%; } } }

  &__main-row {
    display: flex;

    padding: 12px 0px 0px;
    align-items: center;
    justify-content: space-between;
    @media screen and ( max-width: 1199px ) {
      padding-bottom: 20px; } }
  &__info {
    display: inline-flex;
    @media screen and ( max-width: 1499px ) {
      flex-flow: column;
      text-align: center; } }
  &__schedule {
    display: flex;
    position: relative;
    margin-right: 40px;

    padding-right: 40px;
    padding-left: 60px;
    align-items: center;

    font-size: 18px;
    font-weight: bold;
    @media screen and ( max-width: 1499px ) {
      padding: 0px;
      margin-bottom: 20px;
      margin-right: 0px; }
    &::after {
      position: absolute;
      content: '';
      height: 100%;
      width: 2px;
      right: 0px;

      background-color: #eaeaea;
      @media screen and ( max-width: 1499px ) {
        display: none; } }
    span {
      font-size: 22px;
      @media screen and ( max-width: 1199px ) {
        font-size: 18px; }
      &.icon {
        margin-right: 30px;

        color: #1282c3;
        font-size: 28px;
        @media screen and ( max-width: 1199px ) {
          margin-right: 10px; } } }
    // &:hover
    //   span
    //     &.icon
 }    //    // color: #00f
  &__contacts {
    display: flex;
    align-items: center;
    margin-right: 114px;
    @media screen and ( max-width: 1499px ) {
      margin-right: 0px; }
    a {
      display: flex;

      align-items: center;

      color: #000;
      font-size: 18px;
      font-weight: bold;
      &:hover {
        color: #1282c3; } }
    span {
      font-size: 22px;
      @media screen and ( max-width: 1199px ) {
        font-size: 18px; }
      &.icon {
        margin-right: 30px;

        color: #1282c3;
        font-size: 26px;
        @media screen and ( max-width: 1199px ) {
          margin-right: 10px; } } } }
  &__quote-btn {
    display: flex;

    padding: 20px 28px;
    align-items: center;
    justify-content: center;

    color: #000;
    font-size: 23px;
    font-weight: 600;

    border: 3px solid #4DA1D2;
    @media screen and ( max-width: 1199px ) {
      font-size: 18px;
      padding: 12px; }
    &:hover {
      // box-shadow: 0px 2px 5px rgba(0,0,0,.5)
      background-color: #4DA1D3; } }
  &__nav-row {
    background-color: #162D44; }
  &__nav-row-container {
    display: flex;

    align-items: center;
    justify-content: space-between;
    @media screen and ( max-width: 1499px ) {
      flex-flow: column; } }
  nav {
    position: relative;
    &::before {
      position: absolute;
      content: '';
      top: 0px;
      left: 0px;
      height: 5px;
      width: 100%;

      background-color: transparent;

      transition: all .3s; }
    &:hover {
      &:before {
        background-color: #1282c3; } }
    ul {
      display: flex; }
    li {
      padding: 30px;
      @media screen and ( max-width: 1199px ) {
        padding: 20px 12px; } }
    a {
      color: #fff;
      font-weight: bold;
      @media screen and ( max-width: 1199px ) {
        font-size: 18px; }
      &:hover {
        color: #4DA1D2; } } }
  &__smm-block {
    @media screen and ( max-width: 1499px ) {
      margin-bottom: 20px; } }
  &__smm-item {
    font-size: 32px;
    @media screen and ( max-width: 1199px ) {
      font-size: 24px; }
    &:not(:last-child) {
      margin-right: 35px; }
    &::before {
      color: #fff; }
    &:hover {
      &::before {
        color: #4DA1D2; } } }







