.footer {
  display: flex;

  align-items: center;
  justify-content: space-between;
  padding: 20px 10px;

  color: #5A6570;
  font-size: 20px;
  font-weight: bold;

  background-color: #122332;
  &__smm-item {
    font-size: 28px;
    &::before {
      color: #5A6570; }
    &:not(:last-child) {
      margin-right: 30px; }
    &:hover {
      &::before {
        color: #4DA1D3; } } }
  &__privacy {
    color: #5A6570; } }
