h1 {
  margin-bottom: 20px;

  font-size: 65px;
  font-weight: bold; }
h2 {
  margin-bottom: 75px;

  color: #000;
  font-size: 48px;
  font-weight: bold;
  text-align: center; }

.main-screen {
  display: flex;
  min-height: 600px;
  height: calc(100vh - 224px);

  align-items: center;

  background-image: url(https://ll-code-test.s3.amazonaws.com/images/slide1.jpg);
  background-repeat: no-repeat;
  background-size:  cover;
  background-position: center bottom;
  &__content {
    max-width: 50%;
    color: #fff;
    @media screen and ( max-width: 1699px ) {
      max-width: 70%; }
    p {
      margin-bottom: 20px;

      font-weight: 500;
      font-size: 27px;
      line-height: 1.5; }
    a {
      display: inline-flex;
      color: #fff;

      padding: 20px 50px;
      align-items: center;
      justify-content: center;

      border: 4px solid #fff;
      border-radius: 4px;
      &:hover {
        background-color: #4DA1D3; }
 } } }      //   box-shadow: 5px 5px 5px rgba(0,0,0,.5)

.team {
  padding: 65px 0px 0px;
  &__list {
    display: flex;
    margin-bottom: 50px;

    justify-content: space-between;
    @media screen and ( max-width: 1199px ) {
      flex-flow: wrap; } }
  &__card {
    // padding-top: 140px
    width: 25%;
    margin: 0px 20px;
    @media screen and ( max-width: 1199px ) {
      width: calc(50% - 40px);
      margin-bottom: 40px; } } }

.card {
  padding-top: 140px;

  text-align: center;
  &__content {
    position: relative;
    padding: 140px 20px 50px;

    background-color: #f6f6f6;
    &::after {
      position: absolute;
      content: '';
      height: 202px;
      width: 202px;
      top: -131px;
      left: calc(50% - 101px);

      background-color: #fff;

      border-radius: 50%;

      z-index: 10; } }
  &__img {
    position: absolute;
    top: -116px;
    left: calc(50% - 85px);

    height: 170px;
    width: 170px;
    margin: 0px auto;

    border-radius: 50%;

    transition: all .3s;
    overflow: hidden;
    z-index: 20;
    img {
      width: 100%; }
    &::after {
      position: absolute;
      content: '';
      top: 0px;
      left: 0px;
      width: calc(100% - 12px);
      height: calc(100% - 12px);
      border: 6px solid transparent;
      border-radius: 50%; }
    &:hover {
      margin-top: -10px;
      &::after {
        border-color: #4DA1D3; } } }
  &__name {
    margin-bottom: 15px;

    font-weight: bold;
    @media screen and ( max-width: 1499px ) {
      font-size: 18px; } }
  &__position {
    margin-bottom: 20px;

    color: #4DA1D2;
    font-size: 20px;
    font-weight: bold;
    @media screen and ( max-width: 1499px ) {
      font-size: 18px; } }
  &__tel {
    color: #000;
    font-size: 24px;
    font-weight: bold;
    @media screen and ( max-width: 1499px ) {
      font-size: 18px; }
    &:hover {
      color: #4DA1D3; } } }

.post {
  margin-bottom: 80px;

  padding-top: 50px;
  &__item {
    position: relative;
    margin: auto;
    width: 700px;
    height: 600px;
    margin-bottom: 80px;
    @media screen and ( max-width: 1699px ) {
      width: 580px; }
    @media screen and ( max-width: 1499px ) {
      width: 500px;
      height: 500px; } }
  &__container {}
  &__badge {
    position: absolute;
    display: flex;
    padding: 10px 20px 8px;

    color: #fff;
    font-weight: 800;
    text-transform: uppercase;

    background-color: #e27878;

    z-index: 50; }
  &__img-container {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;

    z-index: 10;

    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
      object-position: center; } }
  &__title {
    margin-bottom: 30px;

    color: #000;
    font-size: 28px;
    font-weight: bold;
    line-height: 1.2;

    transition: all .3s; }
  &__date {
    color: #4DA1D3;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase; }
  &__info {
    position: absolute;
    width: 90%;
    top: 70%;
    left: 5%;

    padding: 45px 70px 40px;

    text-align: center;

    background-color: #fff;

    box-shadow: 0px 0px 15px rgba(0,0,0,.3);

    z-index: 100;
    @media screen and ( max-width: 1499px ) {
      padding: 45px 20px 40px; }
    &:hover {
      h3 {
        color: #4DA1D3; } } }
  .owl-dots {
    display: flex;
    justify-content: center; }

  button.owl-dot {
    width: 16px;
    height: 16px;

    background-color: #d1d1d1;

    outline: none;
    &:not(:last-child) {
      margin-right: 10px; }
    &.active {
      background-color: #4DA1D3; } } }

